export default {
	data() {
		return {
			city:'',
			type:'',
			list: [],
			all_list: [],
			default_list: [],
			name: '',
			count: 0,
			free: 0,
			search_key: '', // 搜索关键词
			page: 1,
			isFinish: false,
			allLoaded: false // 分页加载
		}
	},
	onLoad(e) {
		this.city = e.city
		this.type = e.type
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		this.getData()
	},
	methods: {
		getData () {
		  this.$api.club_report({
		    type: this.type,
		    city_name: this.city
		  }).then(res => {
		    uni.hideLoading();
		    if (res.data.code === '200') {
		      this.list = res.data.data.list
		      this.name = res.data.data.name
		      this.count = res.data.data.count
		      this.free = res.data.data.freedom
		      this.all_list = this.list.concat({
		        name: this.city + '自由港',
		        usercount: this.free
		      })
		      this.default_list = this.all_list
		      this.isFinish = true
		    }
		  })
		},
		search () {
		  if (this.search_key.length) {
		    var keyword = this.search_key
		    this.default_list = this.all_list.filter(function (list) {
		        return Object.keys(list).some(function (key) {
		        return String(list[key]).toLowerCase().indexOf(keyword) > -1
		      })
		    })
		  } else {
			uni.showToast({
			    title: '请输入要搜索的内容,不输入内容显示所有',
			    mask: "true",
				icon:'none'
			});
			uni.showLoading({
			    title: '加载中',
				mask:"true"
			});
			this.getData()
		  }
		}
		
	}
}
